import React, { useState } from "react"
import { Router } from "@reach/router"

import ArticlesComponent from "../components/pages/Articles"
import Layout from "../components/Layout"
import { AuthProvider } from "../hooks/auth"
import ArticleComponent from "../components/pages/Article"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const Articles = () => {
	const [breadCrumbList, setBreadCrumb] = useState([
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "news",
			name: translate("breadCrumb.news"),
			isActive: false,
		},
	] as IBreadCrumbOption[])

	const setTitle = (title?: string) => {
		const newBreadCrumb = [...breadCrumbList]
		if (title) {
			newBreadCrumb[1].isActive = true
			newBreadCrumb.push({
				url: "breadCrumbArticle",
				name: title,
				isActive: false,
			})
			setBreadCrumb(newBreadCrumb)
		} else if (breadCrumbList.length > 2) {
			newBreadCrumb[1].isActive = false
			newBreadCrumb.pop()
			setBreadCrumb(newBreadCrumb)
		}
	}
	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="News"
				description="Somos uma empresa de consultoria de qualidade especializada em soluções empresariais, formada por equipe multidisciplinar nas áreas de farmácia, química, engenharia, arquitetura, TI, meio ambiente e muitos outros."
			/>
			<AuthProvider>
				<Router basepath="/news">
					<ArticlesComponent titleCallback={setTitle} path="/" isNews={true} />
					<ArticleComponent titleCallback={setTitle} path="/preview" />
				</Router>
			</AuthProvider>
		</Layout>
	)
}

export default Articles
